import React from "react";

function withSection(WrappedComponent, title, className) {
  return class extends React.Component {
    state = { showCaret: false };
    constructor(props) {
      super(props);
      this.ref = React.createRef();
    }

    setCaret = () => {
      this.setState(prevState => ({
        showCaret: !prevState.showCaret
      }));
    };

    render() {
      return (
        <div
          className={className}
          ref={this.ref}
          onMouseOver={this.setCaret}
          onMouseOut={this.setCaret}
        >

          <div className="section-title">{title}</div>
          <WrappedComponent {...this.props} />
        </div>
      );
    }
  };
}

export default withSection;
