import React from "react";
import withSection from "./withSection";
import { OutreachVoice, PermissionsAccess, ProtectReputation } from "./sliders";
import BrexBrand from '../../header/BrexBrand';

class SectionOptimization extends React.Component {
  render() {
    return (
      <React.Fragment>
        {this.props.onUpdate && (
          <React.Fragment>
            <OutreachVoice onUpdate={this.props.onUpdate} />
            <PermissionsAccess onUpdate={this.props.onUpdate} />
            <ProtectReputation onUpdate={this.props.onUpdate} />
            <BrexBrand />
          </React.Fragment>
        )}
      </React.Fragment>
    );
  }
}

export default withSection(SectionOptimization, "Optimization Integration", 'section optimization');
