import React from "react";
import { Consumer } from "../../roi.context";
import SliderWrapper from "./withSlider";

const strDescriptions = [
  "Completely open access, reps have permission to do anything (create and publish sequences, see all records, configure new rulesets, change the Plug-in, etc.)",
  "Restricted prospects to only being in one sequence at a time",
  "Reconfigured profiles allow reps to only see Accounts and Prospects that they own",
  "Build triggers to match internal sales processes regarding ownership",
  "No emails sent to any prospects not owned by Outreach user"
];

const PermissionsAccess = props => (
  <Consumer>
    {({ permissionsAccess }) => (
      <SliderWrapper
        label="Hierarchy/access permissions implementation"
        descriptions={strDescriptions}
        onChange={props.onUpdate}
        keyName="permissionsAccess"
      />
    )}
  </Consumer>
);

export default PermissionsAccess;
