import React from "react";
import { Consumer } from "../../roi.context";
import SliderWrapper from "./withSlider";

const strDescriptions = [
  "Protect what? 0%",
  "Implemented unsubscribe links on all emails",
  "Implemented process to throttle suspended users",
  "No user suspensions in 30-days",
  "No user suspensions in 90-days"
];

const DomainReputation = props => (
  <Consumer>
    {({ protectReputation }) => (
      <SliderWrapper
        label="Protecting your domain reputation"
        descriptions={strDescriptions}
        onChange={props.onUpdate}
        keyName="protectReputation"
      >
      </SliderWrapper>
    )}
  </Consumer>
);

export default DomainReputation;
